<template>
    <div class="main-container">
        <div class="header-container">
            <button class="menu-button" @click="toggleSideMenu">
                MENU
            </button>
        </div>
        <div v-if="store.state.is_collapsed" class="overlay" @click="toggleSideMenu">
            <span class="overlay-text-1">STICK
                TO YOUR HABITS</span>
            <div class="menu-back-decorate"></div>
            <div class="menu-back-decorate-tiktok"></div>
        </div>


        <!-- 使用transition包裹SideMenu，添加el-aside的定位和z-index -->
        <transition name="slide">

            <SideMenu v-if="store.state.is_collapsed" class="side-menu"
                :class="{ 'is-visible': store.state.is_collapsed }" />

        </transition>
        <div class="main-content">
            <router-view></router-view>
        </div>
    </div>

</template>

<script setup>
import SideMenu from '../components/mainbox/SideMenu.vue'
import { reactive } from 'vue'
import { ref } from 'vue'
import store from '@/store/index'

const toggleSideMenu = () => {
    if (store.state.is_collapsed)
        store.commit("changeCollapsed", false)
    else
        store.commit("changeCollapsed", true)
}

</script>

<style>
@keyframes menuIn {

    0% {
        transform: translateY(-100%);
        width: 0%;
    }

    50% {
        transform: translateY(0);
        width: 1%;
    }

    100% {
        transform: skew(-21deg);
        width: 20vw;
    }
}

@keyframes fadeInBackground {

    0% {
        background-color: rgba(1, 36, 78, 0);
        /* 完全透明 */
    }

    50% {
        background-color: rgba(1, 36, 78, 0.8);
        /* 完全透明 */
    }

}

@keyframes fadeIn {

    0%,
    50% {
        color: rgba(255, 255, 255, 0);
        text-shadow: 6px 3px 2px rgba(0, 0, 0, 0);
    }

    100% {
        color: #ffffff;
        text-shadow: 6px 3px 2px rgba(0, 0, 0, 0.432);
    }
}

.menu-button {
    border: 0.2vw solid rgba(7, 133, 138, 0.8);
    position: fixed;
    top: 2%;
    left: 2%;
    height: 8vh;
    width: 10vw;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    text-align: center;
    font-size: min(4vh, 4vw);
    font-weight: bold;
    transition: .3s;
    z-index: 0;
    font-family: inherit;
    color: rgba(1, 36, 78, 0.8);
    opacity: 0.5;
}

.menu-button::before {
    content: '';
    width: 0;
    height: 100vh;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(21deg);
    background: rgba(7, 133, 138, 0.8);
    transition: .5s ease;
    display: block;
    z-index: -1;
    opacity: 1;
}

.menu-button:hover::before {
    width: 120%;
    opacity: 1;
}

.menu-button:hover {
    color: #111;
    opacity: 1;
}


.side-menu {
    animation: menuIn 0.5s cubic-bezier(.36, .07, .19, .97);
    transform-origin: 50% 50%;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(1, 36, 78, 0.8);
    z-index: 500;
    /* 确保遮罩层位于其他元素上方 */
    animation: fadeInBackground 0.5s cubic-bezier(.36, .07, .19, .97);

}


.overlay-text-1 {
    color: #ffffff;
    position: absolute;
    top: 25%;
    left: 48%;
    transform: translate(-50%, -50%);
    font-size: 10vh;
    font-weight: bold;
    text-shadow: 6px 3px 2px rgba(0, 0, 0, 0.432);

    z-index: 1000;
    transform: skew(-21deg);
    animation: fadeIn 0.5s cubic-bezier(.36, .07, .19, .97);
}

.slide-enter-active,
.slide-leave-active {
    transition: opacity 0.3s;
}

.slide-enter,
.slide-leave-to {
    opacity: 0;
}

/* 页面主容器 */
.main-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

/* 头部容器 */
.header-container {
    flex: 0 1 auto;
    /* 不拉伸，按内容大小 */
    height: 10vh;
}

@keyframes menuBackDecorateIn {

    0% {
        transform: translateY(-100%);
        ;
        width: 0%;
    }

    50% {
        width: 1%;
        transform: translateY(0);
    }

    100% {}
}

.menu-back-decorate {
    height: 100%;
    width: 5%;
    left: 19%;
    background: rgb(65, 65, 65);
    position: fixed;
    z-index: 502;
    transform: skew(-21deg);
    animation: menuBackDecorateIn 0.5s cubic-bezier(.36, .07, .19, .97);

}

.menu-back-decorate-tiktok {
    height: 100%;
    width: 5%;
    left: 19%;
    background: rgb(0, 166, 172);
    position: fixed;
    z-index: 501;
    transform: skew(-100deg);
    animation: menuBackDecorateIn 0.5s cubic-bezier(.36, .07, .19, .97);

}



body {
    background-color: #52616b;
}
</style>